.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.card {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 25px rgba(0,0,0,0.2);
  text-align: center;
  width: 320px;
}

h1 { color: #333; margin-bottom: 0.5rem; }
p { color: #666; font-size: 0.9rem; }

.input-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1.5rem 0;
}

input {
  width: 80px;
  padding: 10px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 1.2rem;
  text-align: center;
  outline: none;
  transition: border-color 0.3s;
}

input:focus { border-color: #667eea; }

.plus { font-size: 1.5rem; font-weight: bold; color: #764ba2; }

.btn-soma {
  width: 100%;
  padding: 12px;
  background: #764ba2;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s;
}

.btn-soma:hover { background: #5a377d; }

.result-area {
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid #eee;
}

.result-value {
  font-size: 2rem;
  font-weight: bold;
  color: #764ba2;
}